@media screen and (min-width: 500px) {

}

@media screen and (max-width: 500px) {
    
}

.App {
  text-align: center;
}

.m_forme {
    position: fixed;
    left:-50%;
    bottom: -14%;
    height: 50%;
    z-index: 100;
    overflow: hidden;
}

.m_rainbow {
    position: fixed;
    bottom: -10%;
    height: 60%;
    left: -50%;
    z-index: 98;
}

.m_poteaux {
    position: fixed;
    height: 70%;
    z-index: 90;
    bottom: 10%;
    left: 5%;
}

.m_montagne {
    position: fixed;
    height: 60%;
    bottom: -5%;
    left:-40%;
    z-index: 90;
}

.m_pyramide1 {
    position: fixed;
    height: 50%;
    left: -80%;
    bottom: 30%;
    z-index: 80;
}


.m_pyramide2 {
    position: fixed;
    height: 50%;
    left: -40%;
    bottom: 20%;
    z-index: 80;
}

.sun {
    position: absolute;
    z-index: 1;
    animation: App-logo-spin infinite 120s linear;
    overflow: hidden;

    @media screen and (min-width: 500px) {
        left: -1300px;
        top: -1250px;
        height: 170vw;
    }
    
    @media screen and (max-width: 500px) {
        position: fixed;
        height: 150%;
        left: calc(-125%);
        top: calc(-75%);
    }
}

.pyramides {
    position: absolute;
    z-index: 2;

    @media screen and (min-width: 500px) {
        width: 110vw;
        left: -350px;
    }
    
    @media screen and (max-width: 500px) {
        height: 100vw;
        left: -500px;
        top: 100px;
    }
}

.eye1 {
    position: relative;
    width: 30px;
    border-radius: 100%;
    background-color: white;
    z-index: 10000;
}

.rainbow {
    position: absolute;
    bottom: -100px;
    z-index: 100;
    right: -100px;

    @media screen and (min-width: 500px) {

    }
    
    @media screen and (max-width: 500px) {
        bottom: 50px;
        img {
            height: 50%;
        }
    }
}

.forme1 {
    position: absolute;
    bottom: -100px;
    z-index: 100;
    left: -100px;
}

.poteaux {
    position: absolute;
    z-index: 98;

    @media screen and (min-width: 500px) {
        left: 250px;
    }
    
    @media screen and (max-width: 500px) {
        left: 10px;
    }

}

.poteaux2 {
    position: absolute;
    right: 50px;
    bottom: -20px;
    z-index: 120;
}

.forme2 {
    position: absolute;
    z-index: 99;
    bottom: 40px;
    left: -180px;
}

.montagne {
    position: absolute;
    width: 2300px;
    bottom: -100px;
    left: -400px;
    z-index: 3;
}

.App-logo {
    position: absolute;
    z-index: 1000;

    @media screen and (min-width: 500px) {
        top: -5vmin;
        height: 15vmin;
    }

    @media screen and (max-width: 500px) {
        position: fixed;
        top: -8vmin;
        left: 50% - calc(50% / 2);
        height: 13vh;
    }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
